import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class NewsServiceService {
  url:string;
  constructor( private http:HttpClient ) { }
  getNews(){
    return this.http.get("https://www.apiopen.top/journalismApi")
  }
}
